import 'bootstrap/dist/css/bootstrap.min.css';
import './App.css';
import React from "react";
import { 
  Button,
  Container,
  Row,
  Col,
  Accordion,
  Card
} from "react-bootstrap";
import komaba from "./assets/komaba.jpeg";
import logo from "./assets/logo.png";
import BootstrapSwitchButton from 'bootstrap-switch-button-react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faCalendarDay, faAppleAlt, faBullhorn, faFlask, faGlobe, faGraduationCap, faBriefcase, faChalkboardTeacher, faMinus, faPlus, faSearch } from '@fortawesome/free-solid-svg-icons'
import { faUsers } from '@fortawesome/free-solid-svg-icons'

import { Schedule } from "./Schedule";

export default class App extends React.Component {
  constructor(props){
    super(props);
    this.state={
      mode:"time",
      inputText:"",
      filterWord:"",
    }
    this.renderIcon = this.renderIcon.bind(this);
    this.getEventTerm = this.getEventTerm.bind(this);
  }

  renderIcon(key){
    switch(key){
      case "food": return faAppleAlt;
      case "media": return faBullhorn;
      case "science": return faFlask;
      case "international": return faGlobe;
      case "education": return faGraduationCap;
      case "business": return faBriefcase;
      case "semi": return faChalkboardTeacher;
    }
  }

  getEventTerm(date, which){
    let hm = date.split(":");
    let start = new Date(`2021-04-${which===4?"04":"11"}T${hm[0]}:${hm[1]}:00+0900`);
    let end = new Date(`2021-04-${which===4?"04":"11"}T${hm[0]}:${hm[1]}:00+0900`);
    end.setMinutes(end.getMinutes()+20)
    return `${start.getHours()}:${start.getMinutes() === 0 ? "00" : start.getMinutes()}〜${end.getHours()}:${end.getMinutes() === 0 ? "00" : end.getMinutes()}`
  }

  renderGenreSchedule(){
    let container = []
    for (const [key, val] of Object.entries(Schedule)){
      let group = Schedule[key].group;
      let groups = [];
      for(let i = 0; i < group.length; i++){
        let content = group[i];
        let contents = [];
        for(let j = 0; j < content.content.length; j++){
          contents.push(
            <>
              <div className="d-flex flex-row justify-content-between">
                <p className="align-self-center" style={styles.genre_fac}>{content.content[j].name}</p>
                <div className="d-flex flex-row">
                  <button className="genre_intro">紹介ページ</button>
                  <button className="genre_zoom">zoomに参加</button>
                </div>
              </div>
              <hr style={{backgroundColor: "rgba(255,255,255,0.25)", height:1}}/>
            </>
          )
        }
        let f_time = [
          <p style={styles.genre_date}>4/4</p>
        ]
        content["time"][0].forEach(date=>{
          f_time.push(<p style={styles.genre_time}>{this.getEventTerm(date, 4)}</p>)
        })
        let s_time = []
        if(content["time"].length===2){
          s_time.push(<p style={styles.genre_date}>4/11</p>)
          content["time"][1].forEach(date=>{
            s_time.push(<p style={styles.genre_time}>{this.getEventTerm(date, 11)}</p>)
          })
        }
        groups.push(
          <>
            <p className="genre_group">Group{i+1}</p>
            <Row style={{marginBottom:20}}>
              <Col md={4}>
                <div className="d-flex flex-row">
                  <div>
                    {f_time}
                  </div>
                  <div>
                    {s_time}
                  </div>
                </div>
              </Col>
              <Col md={8}>
                {contents}
              </Col>
            </Row>
          </>
        )
      }
      container.push(
        <div>
          <div className="d-flex flex-row" style={styles.genre_header}>
            <FontAwesomeIcon icon={this.renderIcon(key)} color="white" style={styles.genre_icon}/>
            <h3 style={styles.genre_title}>{Schedule[key]["title"]}</h3>
          </div>
          {groups}
        </div>
      )
    }
    return container;
  }

  handleFilter(filter){
    this.setState({
      inputText:filter
    });
  }

  renderGroupSchedule(){
    let data = []
    let names = []
    for (const [key, val] of Object.entries(Schedule)){
      Schedule[key].group.forEach(group=>{
        group.content.forEach((content)=>{
          data.push({
            time:group.time,
            name:content.name,
            link:content.link,
            zoom:content.zoom,
            icon:key,
            genre:Schedule[key].title,
          })
        })
      })
    }
    let container = [];
    data.forEach(group=>{
      {
        container.push(
            group.name.includes(this.state.filterWord) || group.genre.includes(this.state.filterWord) ? 
            <Col lg={6} style={styles.accordion_bg}>
              <CustomAccordion group={group} renderIcon={this.renderIcon} getEventTerm={this.getEventTerm}/>
            </Col>
            : null
        )
      }
    })
    return(
      <>
        <form onSubmit={e=>{e.preventDefault();}}>
          <div className="d-flex flex-row align-items-center" style={{marginTop:16}}>
            <input 
              maxLength={30} type="text" 
              name="filterWord" className="group_filter" 
              placeholder="団体名やジャンルを入力"
              value={this.state.inputText}
              onChange={e=>{e.preventDefault();this.handleFilter(e.target.value)}}   
            />
            <FontAwesomeIcon icon={faSearch} color="white" className="search_icon" onClick={()=>{
              this.setState({filterWord:this.state.inputText})
            }}/>
          </div>
        </form>
        <p style={{color:"white", margin:0, fontWeight:"bold", marginTop:16}}>{this.state.filterWord !== "" && "「"+this.state.filterWord+"」"+"の検索結果:"}</p>
        <Row style={{marginTop:20}}>
          {container}
        </Row>
      </>
    )
  }

  renderTimestake(){
    let stakePoints = ["10:00", "10:20", "10:40", "11:00", "11:20", "11:40", "12:00", "12:20", "12:40", "13:00", "13:20", "13:40"]
    let container = []
    for (const [i, value] of stakePoints.entries()) {
      container.push(
        <div className="d-flex flex-column align-items-center stake_container">
          <p className="stake_text">{value}</p>
          <div className="stake"/>
        </div>
      )
    }
    return container
  }
  
  renderTimeline(){
    return(
      <Container style={{padding:0, marginTop:32}}>
        <div className="timeline" style={{position:"relative"}}>
          <hr style={{backgroundColor: "rgba(255,50,0,0.5)", height:2, marginTop:60,marginBottom:30, width:2280}}/>
          <hr style={{backgroundColor: "rgba(255,165,0,0.5)", height:2, marginTop:0,marginBottom:30, width:2280}}/>
          <hr style={{backgroundColor: "rgba(255,220,0,0.5)", height:2, marginTop:0,marginBottom:30, width:2280}}/>
          <hr style={{backgroundColor: "rgba(0,125,100,0.5)", height:2, marginTop:0,marginBottom:30, width:2280}}/>
          <hr style={{backgroundColor: "rgba(135,206,235,0.5)", height:2, marginTop:0,marginBottom:30, width:2280}}/>
          <hr style={{backgroundColor: "rgba(0,100,235,0.5)", height:2, marginTop:0,marginBottom:30, width:2280}}/>
          <hr style={{backgroundColor: "rgba(128,40,128,0.5)", height:2, marginTop:0,marginBottom:30, width:2280}}/>
          <div className="d-flex flex-row" style={{position:"absolute", top:0}}>
            {this.renderTimestake()}
          </div>
        </div>
        <div className="d-flex flex-row align-items-center">
            <button className="date_switch">4/4</button>
            <button className="date_switch">4/11</button>
        </div>
      </Container>
    )
  }

  render(){
    return (
      <body className="body">
        <header style={styles.header}>
          <div style={styles.header_overlay}>
            <Container>
              <img src={logo} style={styles.header_logo}/>
              <h1 className="header_title">UT-BASE主催<br/>オンライン合同新歓</h1>
              <p className="header_caption">
                UT-BASEは皆さんが「夢中になれる場所」を探すお手伝いをするため、統合的なオンライン新歓を実施してきました。<br/>
                そしてUT-BASEはこの度、春のオンライン合同新歓を開催します。<br/>
                参加方法は簡単！紹介ページを見て気になった団体の「説明会に参加」ボタンをクリックしてZoomに接続するだけ。<br/>
                さぁ、東大で最高の出会いを見つけよう。
              </p>
              <div className="d-flex flex-row">
                <div className="left_date">
                  <div style={styles.date_head}>第一回</div>
                  <div style={styles.date_box}>4/4</div>
                </div>
                <div className="right_date" style={{marginLeft:16}}>
                  <div style={styles.date_head}>第二回</div>
                  <div style={styles.date_box}>4/11</div>
                </div>
              </div>
            </Container>
          </div>
        </header>
        <main style={styles.main}>
          <Container>
            <div className="d-flex flex-row">
              <div className={`d-flex flex-row mode_switch ${this.state.mode === "time" ? "underline" : "grayed"}`} style={{marginRight:32}} onClick={()=>{this.setState({mode:"time"})}}>
                <FontAwesomeIcon icon={faCalendarDay} color={"white"} style={styles.mode_icon}/>
                <p style={styles.mode_text}>開催時間別に表示</p>
              </div>
              <div className={`d-flex flex-row mode_switch ${this.state.mode === "each" ? "underline" : "grayed"}`} onClick={()=>{this.setState({mode:"each"})}}>
                <FontAwesomeIcon icon={faUsers} color={"white"} style={styles.mode_icon}/>
                <p style={styles.mode_text}>団体別に表示</p>
              </div>
            </div>
          </Container>
          {
            this.state.mode === "time" ? 
            <Container>
              <div style={styles.main_container}>
                <h2 style={styles.main_title}>タイムライン</h2>
                {this.renderTimeline()}
              </div>
              <div style={styles.main_container}>
                <h2 style={styles.main_title}>ジャンル別スケジュール</h2>
                {this.renderGenreSchedule()}
              </div>
            </Container> : 
            <Container>
              <div style={styles.main_container}>
                <h2 style={styles.main_title}>団体を検索</h2>
                {this.renderGroupSchedule()}
              </div>
            </Container>
          }
        </main>
      </body>
    )
  };
}

class CustomAccordion extends React.Component {
  constructor(props){
    super(props);
    this.state={
      toggled:false
    }
  }   

  render(){
    let f_time = [
      <p style={styles.group_date}>4/4</p>
    ]
    this.props.group.time[0].forEach(date=>{
      f_time.push(<p style={styles.group_time}>{this.props.getEventTerm(date, 4)}</p>)
    })
    let s_time = []
    if(this.props.group.time.length===2){
      s_time.push(<p style={styles.group_date}>4/11</p>)
      this.props.group.time[1].forEach(date=>{
        s_time.push(<p style={styles.group_time}>{this.props.getEventTerm(date, 11)}</p>)
      })
    }

    return(
      <Accordion>
        <Accordion.Toggle as={Card.Header}  variant="link" eventKey={this.props.group.name} className="accordion_toggle"
          onClick={()=>{this.setState({toggled:!this.state.toggled})}}
        >
          <div className="d-flex flex-row justify-content-between" style={{height:36}}>
            <p style={styles.accordion_title}>{this.props.group.name}</p>
            <FontAwesomeIcon icon={this.state.toggled ? faMinus : faPlus} color="white" style={styles.toggle_icon}/>
          </div>
        </Accordion.Toggle>
        <Accordion.Collapse eventKey={this.props.group.name} className="accordion_collapse">
          <div className="d-flex flex-row" style={{marginLeft:15, marginRight:15, paddingTop:15}}>
            <div className="d-flex flex-row" style={{width:200}}>
              <div>
                {f_time}
              </div>
              <div>
              {s_time}
              </div>
            </div>
            <div>
              <div className="d-flex flex-row" style={{marginTop:4}}>
                <p style={styles.group_genre_text}>ジャンル:&nbsp;</p>
                <FontAwesomeIcon icon={this.props.renderIcon(this.props.group.icon)} color="white" style={styles.group_icon}/>
                <p style={styles.group_genre_text}><span style={{marginLeft:4}}>{this.props.group.genre}</span></p>
              </div>
              <div className="d-flex flex-column">
                <button className="genre_intro">紹介ページ</button>
                <button className="genre_zoom" style={{marginTop:12}}>zoomに参加</button>
              </div>
            </div>
          </div>
        </Accordion.Collapse>
      </Accordion>
    )
  }
}
  

